body {
    margin: 0;
    padding: 0;
    height: 100vh;
    background: linear-gradient(to bottom right, #f6d365, #fda085);
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    font-family: Arial, sans-serif;
}

.circle {
    position: absolute;
    border-radius: 50%;
    background: rgba(255, 255, 255, 0.5);
    transition: transform 0.5s ease, opacity 0.5s ease;
}

.circle.small {
    width: 100px;
    height: 100px;
    top: 10%;
    left: 20%;
}

.circle.medium {
    width: 200px;
    height: 200px;
    bottom: 15%;
    right: 25%;
}

.circle.large {
    width: 300px;
    height: 300px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.exploded {
    transform: scale(2) translate(0, -200px);
    opacity: 0;
}